
import XHeader from 'vux/src/components/x-header/index.vue'

import {api} from '@/service/api'

export default {
name:'More',
components:{
XHeader
},
data(){
return {
recommendList:'',
weekendList:''
}
},
mounted(){
api().then(res=>{

this.recommendList= res.data.data.recommendList
this.weekendList= res.data.data.weekendList
})
}
}



