import { api } from '@/service/api'
export default {
    data () {
        return {
            knowlist:[]
        }
    },
    created () {
        api().then(res => {
            let knowinfo = res.data.data.recommendList[0].children.infoData.knowData
            this.knowlist = knowinfo
        })
    },
    computed: {
        abc(){
            return this.$store.state.a
        }
    },
    methods:{
        // changea(x){
        //     console.log(x)
        //     this.$store.commit({//提交mutations
        //         type:'changeA',
        //         can:x
        //     })
        // }
        changea(x){
            this.$store.dispatch({//触发actions
                type:'changeA',
                can:x
            })
        }
    }
}



